const ChatList = ({ messages }) => {
  return (
    <ul>
      <style jsx>{`
        ul {
          height: 100%;
          max-width: 80%;
          display: flex;
          flex-direction: column-reverse;
        }
        li {
            font-size: 1.6rem;
          list-style: none;
          margin: 20px;
        }
      `}</style>
      {messages.reverse().map(msg => {
        return (
          <li>
            <strong>{msg.author}</strong> : {msg.text}
          </li>
        );
      })}
    </ul>
  );
};

export default ChatList;
